.modalBackground {
  width: 100vw;
  height: 100vh;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items:flex-start;
}

.modalContainer {
  width:80%;
  height:70%;
  border-radius: 12px;
  background-color: white;
  box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
  display:flex;
  flex-direction: column;
  padding: 25px;
  overflow: auto;
}

.column-container {
  display:grid;
  grid-template-columns: 1fr 1fr;
}

.confirmation-message {
  color: red;
  font-weight: bold;
  font-size: 180%;
}